'use client';

import { useState } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { z } from 'zod';
import { Button } from '@/components/ui/button';
import {
  Card,
  CardContent,
  CardHeader,
} from '@/components/ui/card';
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { useFirestore, useUser, useMemoFirebase } from '@/firebase';
import { doc } from 'firebase/firestore';
import { setDocumentNonBlocking } from '@/firebase/non-blocking-updates';
import { useToast } from '@/hooks/use-toast';
import { useDoc } from '@/firebase/firestore/use-doc';
import { Separator } from '@/components/ui/separator';
import { RadioGroup, RadioGroupItem } from '@/components/ui/radio-group';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import Link from 'next/link';
import { ChevronRight, ShoppingBag, User as UserIcon, CreditCard, Gift, FileText } from 'lucide-react';


const profileFormSchema = z.object({
  firstName: z.string().min(1, 'First name is required'),
  lastName: z.string().min(1, 'Last name is required'),
  gender: z.enum(['male', 'female']).optional(),
  mobileNumber: z.string().optional(),
});


type UserProfile = {
  firstName: string;
  lastName: string;
  email: string;
  gender?: 'male' | 'female';
  mobileNumber?: string;
};

function ProfileForm({ user, userProfile, userDocRef }: { user: any, userProfile: UserProfile | null, userDocRef: any }) {
    const { toast } = useToast();
    const [isEditing, setIsEditing] = useState(false);

    const form = useForm<z.infer<typeof profileFormSchema>>({
        resolver: zodResolver(profileFormSchema),
        values: {
            firstName: userProfile?.firstName || '',
            lastName: userProfile?.lastName || '',
            gender: userProfile?.gender,
            mobileNumber: userProfile?.mobileNumber || '',
        },
    });

    const onSubmit = (values: z.infer<typeof profileFormSchema>) => {
        if (!userDocRef) return;
        
        setDocumentNonBlocking(userDocRef, { 
          ...values,
        }, { merge: true });
    
        toast({
          title: 'Profile Saved',
          description: 'Your profile has been successfully updated.',
        });
        setIsEditing(false);
    };

    return (
        <Card>
            <CardContent className="p-6 md:p-8">
                <Form {...form}>
                    <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-8">
                        <div className="flex items-center justify-between">
                            <h2 className="text-xl font-semibold">Personal Information</h2>
                            {!isEditing ? (
                                <Button variant="link" className="text-primary" onClick={() => setIsEditing(true)}>Edit</Button>
                            ) : (
                                <Button type="submit" size="sm">Save</Button>
                            )}
                        </div>

                        <div className="grid grid-cols-2 gap-6">
                            <FormField
                            control={form.control}
                            name="firstName"
                            render={({ field }) => (
                                <FormItem>
                                <FormLabel>First Name</FormLabel>
                                <FormControl>
                                    <Input {...field} readOnly={!isEditing} />
                                </FormControl>
                                <FormMessage />
                                </FormItem>
                            )}
                            />
                            <FormField
                            control={form.control}
                            name="lastName"
                            render={({ field }) => (
                                <FormItem>
                                <FormLabel>Last Name</FormLabel>
                                <FormControl>
                                    <Input {...field} readOnly={!isEditing} />
                                </FormControl>
                                <FormMessage />
                                </FormItem>
                            )}
                            />
                        </div>
                        
                        <div>
                            <FormLabel>Your Gender</FormLabel>
                            <FormField
                            control={form.control}
                            name="gender"
                            render={({ field }) => (
                                <RadioGroup
                                    onValueChange={field.onChange}
                                    defaultValue={field.value}
                                    className="flex items-center gap-8 mt-2"
                                    disabled={!isEditing}
                                >
                                    <FormItem className="flex items-center space-x-3 space-y-0">
                                        <FormControl>
                                            <RadioGroupItem value="male" />
                                        </FormControl>
                                        <FormLabel className="font-normal">Male</FormLabel>
                                    </FormItem>
                                    <FormItem className="flex items-center space-x-3 space-y-0">
                                        <FormControl>
                                            <RadioGroupItem value="female" />
                                        </FormControl>
                                        <FormLabel className="font-normal">Female</FormLabel>
                                    </FormItem>
                                </RadioGroup>
                            )}
                            />
                        </div>

                        <Separator />

                        <div>
                            <h2 className="text-xl font-semibold mb-4">Contact Information</h2>
                            <FormItem>
                                <FormLabel>Email Address</FormLabel>
                                <FormControl>
                                    <Input readOnly disabled value={user.email || ''} />
                                </FormControl>
                            </FormItem>
                            <FormField
                            control={form.control}
                            name="mobileNumber"
                            render={({ field }) => (
                                <FormItem className="mt-6">
                                <FormLabel>Mobile Number</FormLabel>
                                <FormControl>
                                    <Input {...field} placeholder="+91" readOnly={!isEditing} />
                                </FormControl>
                                <FormMessage />
                                </FormItem>
                            )}
                            />
                        </div>

                    </form>
                </Form>
            </CardContent>
        </Card>
    );
}

function Sidebar({ user, userProfile }: { user: any, userProfile: UserProfile | null }) {
     const getAvatarFallback = () => {
        if (userProfile?.firstName) return userProfile.firstName.charAt(0).toUpperCase();
        if (user?.email) return user.email.charAt(0).toUpperCase();
        return 'U';
    }

    return (
        <div className="space-y-4">
            <Card>
                <CardHeader className="flex flex-row items-center gap-4 p-4">
                    <Avatar className="h-12 w-12">
                        <AvatarImage src={user?.photoURL} />
                        <AvatarFallback>{getAvatarFallback()}</AvatarFallback>
                    </Avatar>
                    <div>
                        <p className="text-sm text-muted-foreground">Hello,</p>
                        <p className="font-bold text-foreground">
                            {userProfile?.firstName && userProfile?.lastName
                                ? `${userProfile.firstName} ${userProfile.lastName}`
                                : user.email}
                        </p>
                    </div>
                </CardHeader>
            </Card>

            <Card>
                <CardContent className="p-2">
                    <Link href="/orders" className="flex items-center justify-between p-3 hover:bg-muted rounded-md transition-colors">
                        <div className="flex items-center gap-4">
                            <ShoppingBag className="h-5 w-5 text-primary" />
                            <span className="font-medium text-foreground">MY ORDERS</span>
                        </div>
                        <ChevronRight className="h-5 w-5 text-muted-foreground" />
                    </Link>
                    <Separator />
                    <div className="p-3">
                         <div className="flex items-center gap-4">
                            <UserIcon className="h-5 w-5 text-primary" />
                            <span className="font-medium text-muted-foreground">ACCOUNT SETTINGS</span>
                        </div>
                        <div className="flex flex-col items-start mt-4 ml-9 space-y-3 text-sm">
                            <Link href="/profile" className="text-primary font-semibold">Profile Information</Link>
                            <Link href="#" className="hover:text-primary text-foreground transition-colors">Manage Addresses</Link>
                            <Link href="#" className="hover:text-primary text-foreground transition-colors">PAN Card Information</Link>
                        </div>
                    </div>
                     <Separator />
                     <div className="p-3">
                         <div className="flex items-center gap-4">
                            <CreditCard className="h-5 w-5 text-primary" />
                            <span className="font-medium text-muted-foreground">PAYMENTS</span>
                        </div>
                        <div className="flex flex-col items-start mt-4 ml-9 space-y-3 text-sm">
                            <Link href="#" className="hover:text-primary text-foreground transition-colors flex justify-between w-full">
                                <span>Gift Cards</span>
                                <span className="font-semibold text-primary">â‚¹0</span>
                            </Link>
                            <Link href="#" className="hover:text-primary text-foreground transition-colors">Saved UPI</Link>
                            <Link href="#" className="hover:text-primary text-foreground transition-colors">Saved Cards</Link>
                        </div>
                    </div>
                    <Separator />
                     <div className="p-3">
                         <div className="flex items-center gap-4">
                            <FileText className="h-5 w-5 text-primary" />
                            <span className="font-medium text-muted-foreground">MY STUFF</span>
                        </div>
                    </div>
                </CardContent>
            </Card>
        </div>
    )
}


export default function ProfilePage() {
  const { user, isUserLoading } = useUser();
  const firestore = useFirestore();

  const userDocRef = useMemoFirebase(
    () => (firestore && user ? doc(firestore, 'users', user.uid) : null),
    [firestore, user]
  );

  const { data: userProfile, isLoading: isProfileLoading } = useDoc<UserProfile>(userDocRef);

  if (isUserLoading || isProfileLoading) {
    return <div className="container mx-auto py-12"><p>Loading profile...</p></div>;
  }

  if (!user) {
    return <div className="container mx-auto py-12"><p>Please log in to view your profile.</p></div>;
  }

  return (
    <div className="bg-secondary/50">
        <div className="container mx-auto py-8 md:py-12">
            <div className="grid md:grid-cols-4 gap-8 items-start">
                <div className="md:col-span-1">
                    <Sidebar user={user} userProfile={userProfile} />
                </div>
                <div className="md:col-span-3">
                    <ProfileForm user={user} userProfile={userProfile} userDocRef={userDocRef} />
                </div>
            </div>
        </div>
    </div>
  );
}
